body {
  --blue-color: #00acdc;
  --black-color: #000;
  --grey-color: #585858;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__link {
  width: 40px;
  height: 40px;
}

.nav {
  display: inline-block;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: var(--black-color);
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: var(--blue-color);
}

.nav__link:active {
  color: var(--grey-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

